@charset 'utf-8';
@import '_common';

.blog {
  display: block;
  transition: background-color .3s ease-out;
  padding: 10px 10px 30px;

  @media (max-width: 480px) {
    padding: 0 0 30px;
    width: 100%
  }

  &:hover {
    background-color: $theme-color-main;

    @media (max-width: 480px) {
      background-color: transparent;
    }

    .info {
      p {
        color: $theme-color-dark-primary;
        @media (max-width: 480px) {
          color: $theme-color-text-secondary;
        }

        &.title {
          color: $theme-color-dark-primary;
          @media (max-width: 480px) {
            color: $theme-color-main;
          }
        }
      }

      .tags {
        .tag {
          background-color: $theme-color-dark-primary;
          color: $theme-color-main;

          &::before {
            background-color: $theme-color-main;
          }
        }
      }
    }
  }

  .banner {
    overflow: hidden;
    position: relative;

    img {
      width: 100%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .info {
    padding: 10px 0;

    p {
      color: $theme-color-text-secondary;
      line-height: 1.3;
      word-wrap: break-word;
      transition: color .3s ease-out;
      margin-bottom: 8px;

      &.title {
        color: $theme-color-main;
        font-size: 26px;
        margin-bottom: 4px;
      }
      &.create-date {
        font-size: 14px;
      }
      &.description {
        font-size: 14px;
      }
    }

    .tags {
      margin-bottom: 8px;

      .tag {
        display: inline-block;
        background-color: $theme-color-main;
        color: $theme-color-dark-primary;
        padding: 4px 10px 4px 20px;
        line-height: 1;
        font-size: 14px;
        position: relative;
        border-radius: 9px 2px 2px 9px;
        font-weight: 400;
        margin-bottom: 4px;
        transition: all .3s ease-out;
        user-select: none;

        $dot-size: 6px;
        &::before {
          content: '';
          position: absolute;
          top: (22px - $dot-size) / 2;
          left: (22px - $dot-size) / 2;
          width: $dot-size;
          height: $dot-size;
          border-radius: 50%;
          background-color: $theme-color-dark-primary;
          transition: all .3s ease-out;
        }
      }
    }
  }
}